import {
  appendHeader,
  assignAdminNavbar,
  correctNavbarHref,
  fetchUrl,
  formatCurrency,
  getDateFromTimestamp,
  getTimestampFromDateText,
  getToken,
  logoutBtnOnClick,
  redirectTo,
  reloadDelay,
} from "../../util";
import { ROLES, monthsArr } from "../../util/constants";
import {
  XANO_ADMIN_FINANCE_DETAIL_NEW_ENTRY_URL,
  XANO_ADMIN_FINANCE_DETAIL_URL,
  XANO_ADMIN_SIGN_IN_REDIRECT,
} from "../../util/xano_urls";

// ? prevent 'global' variables from being accessed and overwritten in google inspect console
(() => {
  let UrlGymId, UrlMonth, UrlYear, UrlFinanceId;

  window.onload = async function () {
    logoutBtnOnClick("/admin/sign-in", "sidebar-logout-btn");

    var curUrl = new URL(document.location.href);
    UrlGymId = curUrl.searchParams.get("gymCode") || null;
    UrlMonth = curUrl.searchParams.get("month") || null;
    UrlYear = curUrl.searchParams.get("year") || null;

    if (UrlGymId) correctNavbarHref(UrlGymId);
    else {
      // ? TODO: Change this to a modal showcasing the error
      console.error("No gym id found");
      return;
    }

    const token = getToken();
    if (token) {
      const xano_input = {
        gym_id: UrlGymId,
        month: UrlMonth,
        year: UrlYear,
      };

      let result = await fetchUrl(XANO_ADMIN_FINANCE_DETAIL_URL, "POST", xano_input, token);

      if (result.isFetchOk !== undefined && !result.isFetchOk) {
        console.error(result.message);
        // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
        return;
      }

      const { team_role, ...data } = result;

      if (team_role === ROLES.SUPER_ADMIN || team_role === ROLES.ADMIN) {
        assignAdminNavbar(team_role);
        getSuperAdminData(data, token);
      } else {
        redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=UNAUTHORIZED_USER`);
      }
    } else {
      redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
    }
  };

  async function getSuperAdminData(_data) {
    const { gym, finance, payment_ledger } = _data;

    if (gym) appendHeader(gym);

    initNewEntry();

    if (finance) {
      const { id, additional_entries, ...data } = finance[0];
      if (id) UrlFinanceId = id;
      appendFinanceData(data);
      appendFinanceTable(additional_entries, payment_ledger);
    }
  }

  function appendFinanceData(finance) {
    const { month, year, member_revenue, fixed_expense, gross_revenue, additional_expenses, net_profit } = finance;

    document.getElementById("finance-month").innerText = monthsArr[month - 1];
    document.getElementById("finance-year").innerText = year;
    document.getElementById("financial-gross-gym-revenue").innerText = formatCurrency(member_revenue) || "0";
    document.getElementById("financial-franchise-cut").innerText = formatCurrency(fixed_expense) || "0";
    document.getElementById("financial-net-gym-revenue").innerText = formatCurrency(gross_revenue) || "0";
    document.getElementById("financial-gym-expenses").innerText = formatCurrency(additional_expenses) || "0";
    document.getElementById("financial-net-profit").innerText = formatCurrency(net_profit) || "0";
  }

  function appendFinanceTable(additional_entries = [], payment_ledger = []) {
    const tableContainer = document.getElementById("account-table-container");
    const og_webflow_element = tableContainer.querySelector("#account-table-item");

    const normalizedPaymentLedger = [
      ...payment_ledger.map((p) => ({
        time: p.time,
        description: `${p.name}  (${p.mode_of_payment} | ${getDateFromTimestamp(p.membership_end_date)})`,
        debit: null,
        credit: p.amount,
        user: true,
      })),
    ];

    const mergedArr = additional_entries.concat(normalizedPaymentLedger);

    mergedArr.sort((a, b) => {
      return b.time - a.time;
    });

    mergedArr?.forEach((entry, indx) => {
      const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
      clonedCard.setAttribute("id", "");
      let el = clonedCard.querySelectorAll(".colcontent");

      if (indx % 2 === 0) clonedCard.style.backgroundColor = "#00edf4";

      if (entry && entry.user) clonedCard.style.textDecoration = "underline";

      el[0].innerText = getDateFromTimestamp(entry.time);
      el[1].innerText = entry.description || "";
      el[2].innerText = entry.debit || "";
      el[3].innerText = entry.credit || "";

      tableContainer.append(clonedCard);
    });

    og_webflow_element.remove();
  }

  function initNewEntry() {
    const today = new Date();
    const startDate = new Date(today.getFullYear(), today.getMonth() , 1);
    const lastDate = new Date(today.getFullYear(), today.getMonth() , 31);

    const selectDateEl = document.getElementById("datepicker-input");
    $(selectDateEl)
      .datepicker({
        startDate: startDate,
        endDate: lastDate,
        format: "dd/mm/yyyy",
        language: "en",
        multidate: false,
        orientation: "bottom auto",
      })
      .datepicker("setDate", "now");

    const formBlock = document.getElementById("entry-form-block");
    const newEntryBtn = formBlock.querySelector("#entry-btn");

    newEntryBtn.onclick = () => {
      if (newEntryBtn.disabled) return;
      newEntryBtn.disabled = true;
      newEntryBtn.value = "Submitting...";
      if (!newEntryBtn.classList.contains("disabled")) newEntryBtn.classList.add("disabled");

      submitNewEntry();
    };
  }

  async function submitNewEntry() {
    const $form = $("#entry-form-block");
    if ($form.attr("id") === "entry-form-block") {
      const token = getToken();
      if (token) {
        const date = $form[0][0].value || "";
        const description = $form[0][1].value || "";
        const debit = $form[0][2].value || "";
        const credit = $form[0][3].value || "";

        if (date !== "" && description !== "" && (debit !== "" || credit !== "")) {
          const xano_input = {
            finance_id: UrlFinanceId,
            gym_id: UrlGymId,
            month: UrlMonth,
            year: UrlYear,
            time: getTimestampFromDateText(date),
            description,
            debit,
            credit,
          };

          let result = await fetchUrl(XANO_ADMIN_FINANCE_DETAIL_NEW_ENTRY_URL, "POST", xano_input, token);

          if (result.isFetchOk !== undefined && !result.isFetchOk) {
            console.error(result.message);
            // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);

            $form
              .hide() // optional hiding of form
              .siblings(".w-form-done")
              .hide() // Hide failure
              .siblings(".w-form-fail")
              .show() // Show success
              .text("Failed to add new entry. Please try again - Error: " + result.message);

            return;
          }

          $form
            .hide() // optional hiding of form
            .siblings(".w-form-fail")
            .hide() // Hide failure
            .siblings(".w-form-done")
            .show() // Show success
            .text("Successfully added new entry!");

          reloadDelay();
        }
      }
    }
  }
})();

var Webflow = Webflow || [];
Webflow.push(function () {
  // unbind webflow form handling (keep this if you only want to affect specific forms)
  $(document).off("submit");
  /* Any form on the page */

  $("form").on("change", function () {
    const $form = $(this); // The submitted form
    if ($form.attr("id") === "entry-form-block") {
      const date = $form[0][0].value || "";
      const description = $form[0][1].value || "";
      const debit = $form[0][2].value || "";
      const credit = $form[0][3].value || "";

      const formBlock = document.getElementById("entry-form-block");
      const newEntryBtn = formBlock.querySelector("#entry-btn");

      if (date !== "" && description !== "" && (debit !== "" || credit !== "")) {
        newEntryBtn.disabled = false;
        newEntryBtn.classList.remove("disabled");
      } else {
        newEntryBtn.disabled = true;
        if (!newEntryBtn.classList.contains("disabled")) newEntryBtn.classList.addClass("disabled");
      }
    }
  });

  $("form").on("submit", async function (e) {
    e.preventDefault();
  });
});
